@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.navbar {
  box-shadow: 0px 1px 5px -3px #000000bf;
  -webkit-box-shadow: 0px 1px 5px -3px #000000bf;
  -moz-box-shadow: 0px 1px 5px -3px #000000bf;
}

.nav-wrapper {
  display: flex;
  align-items: center;
  max-width: 1330px;
  padding: 1.5rem 50px;
  justify-content: space-between;
  margin: 0 auto;
}

.mode-toggle {
  display: inline-block;
  cursor: pointer;
}

.fa-moon {
  width: 16px;
}

@media (max-width: 768px) {
  .nav-wrapper{
    padding: 1.5rem 20px;
  }

  .nav-wrapper h2{
    font-size: 1.2rem;
  }
}
.country-card {
  background-color: #fff;
  box-shadow: 0px 0px 5px -1px #000000;
}

.country-img {
  width: 100%;
  height: 160px;
  object-fit: cover;
  vertical-align: top;
}

.country-info {
  padding: 20px;
  color: #000;
}

.country-info h3,
.country-info p {
  margin-bottom: 5px;
}

.country-info p {
  font-size: 14px;
}
.main-container {
  max-width: 1330px;
  margin: 0 auto;
}

.filter-div {
  display: flex;
  justify-content: space-between;
  padding: 30px 50px;
  align-content: center;
}

.filter-div select,
.filter-div input { 
  box-shadow: 0px 0px 5px -1px #000000;
  border: none;
  padding: 15px 35px;
  font-size: 16px;
}

.fa-search {
  position: relative;
  left: 27px;
  top: 2px
}

.search-div {
  position: relative;
  right: 15px;
}

.countries-grid {
  display: grid;
  padding: 0 50px 30px 50px;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 70px;
  grid-row-gap: 40px;
}

@media (max-width: 1200px) {
  .countries-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 70px;
    grid-row-gap: 40px;
  }

}

@media (max-width: 1000px) {
  .countries-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 70px;
    grid-row-gap: 40px;
  }

}

@media (max-width: 768px) {
  .countries-grid {
    grid-template-columns: repeat(1, 1fr);
    grid-column-gap: 70px;
    grid-row-gap: 40px;
  }

  .filter-div {
    display: block;
  }

  .search-div {
    width: 100%;
    left: 0px;
  }

  .fa-search {
    top: 47px;
    left: 10px;
  }

  .filter-div select,
  .filter-div input {
    width: 100%;
    margin: 10px 0;
  }
}
.details-wrapper {
  padding: 0 10%;
  margin: 0 auto;
}

.back-button {
  padding: 10px 20px;
  margin: 50px 0;
}

.fa-long-arrow-alt-left {
  margin-right: 10px;
}

.details-container {
  display: flex;
  align-items: center;
}

.flag,
.details-div {
  width: 50%;
}

.flag {
  box-shadow: 0px 0px 5px -1px #000000;
  margin-right: 50px;
}

.country-name {
  margin-bottom: 50px;
}

.details {
  display: flex;
  margin-bottom: 50px;
}

.details-1,
.details-2 {
  width: 50%;
}

.borders-container {
  display: flex;
  align-items: center;
}

.border-title {
  font-weight: 700;
}

.border-btn {
  margin: 5px;
  padding: 5px 10px;
}

@media (max-width: 1200px) {

  .country-name,
  .details {
    margin-bottom: 20px;
  }

  .details {
    display: block;
  }

  .details-1,
  .details-2 {
    width: 100%;
  }

  .borders-container {
    display: block;
    align-items: center;
  }
}

@media (max-width: 1000px) {
  .details-container {
    display: block;
  }

  .flag,
  .details-div {
    width: 100%;
  }

  .flag {
    margin-bottom: 30px;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



span {
  font-weight: 400;
}

p {
  font-weight: 700;
  margin-bottom: 10px;
}

button,
input,
select {
  border-radius: 5px;
}

button {
  box-shadow: 0px 0px 5px -1px #000000;
  background-color: #fff;
  border: none;
  cursor: pointer;
}



body{
  font-family: 'Nunito Sans', sans-serif;
  background-color: #fafafa;
  color: #111517;
  min-height: 100vh;
}

.dark-mode {
  background-color: #202c37;
  color: #fff;
  min-height: 100vh;
}

.dark-mode .nav-input {
  background-color: #2b3945;
  color: #fff;
}

.dark-mode .country-info {
  background-color: #2b3945;
  color: #fff;
}

.dark-mode .btn {
  background-color: #2b3945;
  color: #fff;
}
